@import "base";

@keyframes apply {
  from {transform: scale(1.0) translate(calc(20% + 20px), calc(-15% - 30px));}
  to {transform: scale(1.1) translate(calc(20% + 20px), calc(-15% - 30px));}
}


body {
	background-color: #FFE0DE;
	color: #00347A;
	font-size: 20px;
}

a {
	text-decoration: none;
	color: #FF8F8B;
}
a.home-button{
	display: block;
	text-align: center;
	width: 100%;
}
a.home-button > img {
	width: 5em;
	height: auto;
}
div#wrapper > h1:first-of-type {
	margin-top: 0rem;
}
h1 {
	text-align: center;
}

#wrapper {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	min-height: 100%;
	width: 100%;
	box-sizing: border-box;
	margin: auto;
	padding: 1em;
	max-width: 1400px;
	font-size: 1.5em;
	overflow-x: hidden;
}

div#wrapper.page-wrapper {
	position: relative;
	max-width: 800px;
}

nav.site-navigation{
	display: flex;
	position: relative;
	width: 100%;
	max-width: 1000px;
	flex-flow: row wrap;
}

nav.site-navigation > a{
	display: block;
	margin-top: 0em;
	position: relative;
	color: #00347A;
}

nav.site-navigation > a:not(:first-child){
	margin-left: 2em;
}

.main-canvas{
	display: flex;
	flex-flow: row wrap;
	justify-content: center;
	min-height: 100%;
	align-items: center;
	align-content: center;
}
.main-canvas > * {
	position: relative;
	flex: 0 0 auto;
	max-height: 80%;
	width: auto;
	max-width: 40%;
	min-width: 150px;
}
div.logo-container{
	position: relative;
	max-width: 50%;
	min-width: 280px;
}

div.logo-container > img {
	height: auto;
	width: 100%;

}
#apply-button{
	right: 0;
	top: 0;
	position: absolute;
	display:flex;
	padding: 1em;
	background-image: url('/uploads/Asset%202@4x-8.png');
	background-size: 100% 100%;
	width: 7.5vw;
	height: 9.2vw;
	justify-content: center;
	text-align: center;
	align-items: center;
	min-width: 64px;
	min-height: 80px;
	transform-origin: center center;
	animation: apply 1s infinite alternate;
	color: #00347A;
}
.mascot {
	max-height: 70%;
	max-width: 40%;
}
footer.fest-tagline {
	text-align: center;
	width: 100%;
	margin-top: 0.75em;
	max-width: 100%;
}

@media only screen and (max-width: 600px) {
  body {
    font-size: 1em;
  }
  nav.site-navigation{
  	justify-content: center;
  	margin-bottom: 0.5em;
  }
  #apply-button {
  }
  div.logo-container{
  	margin-left: 50%;
  	margin-right: 50%;
  }
  .mascot {
  	transform: 1em;
  }
  footer.fest-tagline {
  	margin-top: 0em;
  }
  nav.site-navigation > a {
	  border: 1px solid #00347A;
		padding: 0.375em;
		border-radius: 1em;
		margin-left: 0.25em;
		margin-right: 0.25em;
		margin-top: 0.5em;
	}
	nav.site-navigation > a:hover{
	background-color: #00347A;
	color: #FFE0DE
}
  nav.site-navigation > a:not(:first-child){
	margin-left: 0.25em;
	margin-right: 0.25em;
	}
	nav.site-navigation > a:first-child{
		margin-left: 0;
	}
	nav.site-navigation > a:last-child{
		margin-right: 0;
	}
}